@import './fonts.css';

:root {
	--bg-black-900: #000;
	--bg-black-100: #ddd;
	--bg-black-50: #eff0f4;
	--text-black-900: #000;
	--text-black-700: #555;
	--text-black-600: #666;
	--text-black-300: #bbb;
	--outer-shadow: 3px 3px 3px #d0d0d0, -3px -3px 3px #f8f8f8;
	--outer-shadow-0: 0 0 0 #d0d0d0, 0 0 0 #f8f8f8;
	--inner-shadow: inset 3px 3px 3px #d0d0d0, inset -3px -3px 3px #f8f8f8;
	--color-main: #fb839e;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	outline: none;
}

body {
	font-size: 16px;
	line-height: 1.5;
	overflow-x: hidden;
	font-family: 'Raleway';
	background-color: var(--bg-black-50);
}

:before,:after {
	box-sizing: border-box;
}

a {
	text-decoration: none;
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}
img {
	vertical-align: middle;
	max-width: 100%;
}

.container {
	max-width: 1140px;
	margin: 0 auto;
}

.justify-content-between {
	justify-content: space-between;
}
.outer-shadow {
	box-shadow: var(--outer-shadow);
}
.inner-shadow {
	box-shadow: var(--inner-shadow);
}
.hover-in-shadow {
	position: relative;
	z-index: 1;
	&:hover {
		box-shadow: var(--outer-shadow-0);
		&:after {
			box-shadow: var(--inner-shadow);
		}
	}
	&:after {
		content: '';
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		transition: all 0.3s ease;
	}
}
.header {
	padding: 20px 15px;
	.row {
		display: flex;
		flex-wrap: wrap;
		.logo a {
			display: inline-block;
			text-align: center;
			line-height: 36px;
			height: 40px;
			width: 40px;
			font-size: 24px;
			color: var(--color-main);
			border-radius: 50%;
			border: 2px solid var(--color-main);
			font-weight: 600;
			text-transform: uppercase;
		}
		.hamburger-btn {
			height: 40px;
			width: 40px;
			display: flex;
			align-items: center;
			justify-content: center;
			cursor: pointer;
			border-radius: 50%;
			transition: all 0.3s ease;

			&:after {
				border-radius: 50%;
			}

			span {
				display: block;
				position: relative;
				height: 2px;
				width: 16px;
				background-color: var(--bg-black-900);
				&:before,
				&:after {
					content: '';
					position: absolute;
					left: 0;
					width: 100%;
					height: 100%;
					background-color: var(--bg-black-900);
				}
				&:before {
					top: -6px;
				}
				&:after {
					top: 6px;
				}
			}
		}
	}
}

.nav-menu {
	position: fixed;
	background-color: var(--bg-black-50);
	padding: 0 15px;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: 9;
	overflow-y: auto;
	.close-nav-menu {
		height: 40px;
		width: 40px;
		display: block;
		font-size: 35px;
		line-height: 35px;
		border-radius: 50%;
		position: absolute;
		right: 15px;
		top: 15px;
		cursor: pointer;
		text-align: center;
		color: var(--text-black-600);
		transition: all 0.3s ease;
		&:after {
			border-radius: 50%;
		}
	}
	.nav-menu-inner {
		min-height: 100vh;
		display: flex;
		align-items: center;
		justify-content: center;
		ul {
			li {
				display: block;
				padding: 15px;
				margin-bottom: 20px;
				text-align: center;
				&:last-child {
					margin-bottom: 0;
				}
				a {
					display: inline-block;
					font-size: 28px;
					font-weight: 600;
					color: var(--text-black-700);
					padding: 5px 30px;
					text-transform: capitalize;
					border-radius: 30px;
					transition: all 0.3s ease;

					&:after {
						border-radius: 30px;
					}
					&:active {
						
					}
				}
			}
		}
	}
}